@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff") format("woff");
  src: url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff") format("woff");
  src: url("../fonts/opensansbold.woff2") format("woff2");
}
html {
  height: 100%;
}

body {
  font-family: "Open Sans", "PT Sans", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
}

.advantages {
  position: relative;
}
.advantages::after {
  content: "";
  position: absolute;
  background-image: url("../../img/saw-line.svg");
  background-size: auto 6px;
  background-repeat: repeat-x;
  width: 100%;
  height: 6px;
  top: -31px;
}
@media (min-width: 768px) {
  .advantages::after {
    background-size: auto 17px;
    top: -55px;
    height: 17px;
  }
}
@media (min-width: 1150px) {
  .advantages::after {
    background-size: auto 23px;
    top: -68px;
    height: 23px;
  }
}

.advantages__wrapper {
  margin-right: 30px;
  margin-left: 30px;
  margin-bottom: 58px;
}
@media (min-width: 768px) {
  .advantages__wrapper {
    margin-right: 50px;
    margin-left: 50px;
  }
}
@media (min-width: 768px) {
