}
.advantages__item--likes::before {
  content: "";
  position: absolute;
  background-image: url("../../img/likes-icon.svg");
  width: 34px;
  height: 27px;
  bottom: 61px;
  left: 0;
}
.advantages__item--knit::before {
  content: "";
  position: absolute;
  background-image: url("../../img/knit-icon.svg");
