.button {
  display: block;
  box-sizing: border-box;
  padding-top: 14px;
  padding-bottom: 14px;
  background: #ffffff;
  border: 2px solid #444444;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #444444;
  cursor: pointer;
}
.button:hover {
  background: #444444;
  color: #ffffff;
  transition: 0.2s;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.card {
  border: 1px solid #ececec;
