    grid-template-columns: auto;
    grid-template-rows: 2fr 1fr;
  }
}
.card:hover .card__info {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  transition: 0.2s;
}
.card:active .card__info {
