  }
}
.form__input[type=radio] + .form__mark {
  border-radius: 50%;
}
.form__input[type=radio]:checked + .form__mark::before {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 18px;
  height: 18px;
  background-color: #63d1bb;
  border-radius: 50%;
  content: "";
}
.form__input[type=checkbox]:checked + .form__mark::after {
  position: absolute;
  top: 9px;
  left: 5px;
  width: 24px;
  height: 17px;
  background-image: url("../../img/Tick.svg");
  background-repeat: no-repeat;
  content: "";
}
.form__input:hover, .form__input:active {
  box-shadow: inset 0 -2px 0 #63d1bb;
}

