  opacity: 0.3;
}

.copyright__icon {
  color: #63d1bb;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: 1150px;
}

.extra-list {
  display: none;
}
@media (min-width: 768px) {
  .extra-list {
    display: block;
    position: absolute;
    left: auto;
    right: 0;
  }
}
@media (min-width: 1150px) {
  .extra-list {
    display: flex;
    position: absolute;
    top: 90px;
    left: -35px;
  }
}

.extra-list__item {
  font-size: 15px;
}

@media (min-width: 768px) {
  .extra-list__link {
    display: none;
  }
}
@media (min-width: 1150px) {
  .extra-list__link {
    display: block;
    text-decoration: none;
    color: #000000;
    padding-top: 20px;
    padding-right: 29px;
    padding-bottom: 20px;
    padding-left: 35px;
  }
}
.extra-list__link:hover {
  opacity: 0.6;
}
.extra-list__link:active {
  opacity: 0.3;
}

.extra-list__delivery {
  font-size: 15px;
  color: #444444;
  font-weight: 400;
}
@media (min-width: 1150px) {
  .extra-list__delivery {
    position: absolute;
    white-space: nowrap;
    top: 0;
    right: 0;
    padding-top: 20px;
  }
